<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process261"></a>

<p><br></p>
<p><span class="rvts17">紧急事件（</span><span class="rvts50">A Most Urgent Matter</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:277,Y:404</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：查理（</span><span class="rvts20">Charlie</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助查理寻找到它的玩具球</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1433.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在大教堂的外侧回廊里，可以看到一只叫做查理的狗，这只狗是阿虎的爱犬。使用</span><span class="rvts20"></span><span class="rvts21">动物伙伴</span><span class="rvts20"></span><span class="rvts21">天赋与它交流，询问阿虎的下落，它会告诉你目前最紧急的事情是找到它的玩具球。</span>
<br><span class="rvts21">查理会告诉你，玩具球最后一次发现是在阿虎大人房间的阳台处。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1434.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">如果此时还未救出阿虎大人，则需要找到教堂侧厅的阿维尼（</span><span class="rvts20">Avenny</span><span class="rvts21">），她是阿虎的表妹，说服她，可以获得阿虎房间的钥匙。</span>
<br><span class="rvts21">如果无法说服她，则需要撬锁进阿虎房间了。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1435.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">阿虎房间的位置位于大教堂的右偏房，紧挨着厨房。一旦进入阿虎的房间，会发现他房间十分杂乱，这就意味着这里肯定发生过什么事情。</span>
<br><span class="rvts21">一旦进入房间，会在</span><span class="rvts20">10</span><span class="rvts21">秒内迎来圣骑士的巡查，很明显这些圣骑士是陷害阿虎的圣骑士领导凯姆的手下。这场战斗最终是要打的，即便和平解决，你会被关进兵营的监狱，并没收兵器。之后，逃出监狱还需要面对监狱中更多的狱卒。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1436.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">狗狗的玩具球在这里（</span><span class="rvts20">X:373,Y:465</span><span class="rvts21">），必须使用位移，跳跃，或传送技能才可以拿到它。之后把它交给狗狗查理，它会告诉你关于主人阿虎失踪的一些信息。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



